import React from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import Layout from 'layout';
import { HOME, SHOP_PAGE, CART_PAGE, DETAIL_PAGE, CHECKOUT_PAGE, ORDER_PAGE } from 'config';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

// Import pages index
import Loading from 'UI/Loading';

const HomePage = React.lazy(() => import('pages/HomePage'));
const ErrorPage = React.lazy(() => import('pages/ErrorPage'));
const ShopPage = React.lazy(() => import('pages/ShopPage'));
const DetailPage = React.lazy(() => import('pages/DetailPage'));
const CartPage = React.lazy(() => import('pages/CartPage'));
const CheckoutPage = React.lazy(() => import('pages/CheckoutPage'));
const OrderPage = React.lazy(() => import('pages/OrderPage'));

const App = () => {
  return (
    <React.Fragment>
      <BrowserRouter>
        <Routes>

          <Route path={HOME + '/'} element={<Layout />}>
            <Route index element={
              <React.Suspense fallback={<Loading />}>
                <HomePage />
              </React.Suspense>
            } exact />
            <Route path={SHOP_PAGE} element={
              <React.Suspense fallback={<Loading />}>
                <ShopPage />
              </React.Suspense>
            } />
            <Route path={DETAIL_PAGE} element={
              <Navigate to={SHOP_PAGE} replace />
            } />
            <Route path={DETAIL_PAGE + '/:id/*'} element={
              <React.Suspense fallback={<Loading />}>
                <DetailPage />
              </React.Suspense>
            } />
            <Route path={CART_PAGE} element={
              <React.Suspense fallback={<Loading />}>
                <CartPage />
              </React.Suspense>
            } />
            <Route path={CHECKOUT_PAGE} element={
              <React.Suspense fallback={<Loading />}>
                <CheckoutPage />
              </React.Suspense>
            } />
            <Route path={ORDER_PAGE} element={
              <React.Suspense fallback={<Loading />}>
                <OrderPage />
              </React.Suspense>
            } />
            <Route path={'*'} element={
              <React.Suspense fallback={<Loading />}>
                <ErrorPage error={404} />
              </React.Suspense>
            } />
          </Route>

        </Routes>
      </BrowserRouter>
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
    </React.Fragment>
  );
}

export default App;
