import { createSlice } from '@reduxjs/toolkit';

let initial = {
    items: [],
    totalPay: 0
};

if (localStorage.getItem('cart')) {
    initial = JSON.parse(localStorage.getItem('cart'))
}

const { reducer, actions } = createSlice({
    name: 'cart',
    initialState: initial,
    reducers: {
        addItem(state, action) {
            state.totalPay += action.payload.price * action.payload.amount;

            const idx = state.items.findIndex(x => x.id === action.payload.id);
            if (idx != -1) {
                state.items[idx].amount += action.payload.amount;
            } else {
                state.items.push(action.payload);
            }

            localStorage.setItem('cart', JSON.stringify(state));
            return state;
        },
        reduceItem(state, action) {
            const idx = state.items.findIndex(x => x.id === action.payload);
            if (idx !== -1) {
                state.totalPay = state.totalPay - state.items[idx].price;
                if (state.items[idx].amount > 1) {
                    state.items[idx].amount--;
                } else {
                    state.items = state.items.filter(x => x.id !== action.payload);
                }
            }

            localStorage.setItem('cart', JSON.stringify(state));
            return state;
        },
        removeItem(state, action) {
            const idx = state.items.findIndex(x => x.id === action.payload);
            if (idx !== -1) {
                state.totalPay = state.totalPay - (state.items[idx].price * state.items[idx].amount);
                state.items = state.items.filter(x => x.id !== action.payload);
            }

            localStorage.setItem('cart', JSON.stringify(state));
            return state;
        },
        clearCart(state, action) {
            return {
                items: [],
                totalPay: 0
            };
        }
    }
});

export const { addItem, reduceItem, removeItem, clearCart } = actions;
export default reducer;