import { SERVER_SOCKET, SERVER_API } from "config";
import axios from "axios";
import { useEffect, useState } from "react";
import { toast } from 'react-toastify';
import io from 'socket.io-client';
import Cookies from 'universal-cookie';

import MessageContent from './MessageContent';
import SendForm from "./SendForm";
import MessageHeader from "./MessageHeader";

const cookies = new Cookies();
const socket = io.connect(SERVER_SOCKET + '/support');
if (!localStorage.getItem("supportRoom")) {
    localStorage.setItem("supportRoom", Date.now() + '-' + (Math.random() + 1).toString(36).substring(2));
}

const scrollBottom = () => {
    const chatbox = document.getElementById("chatbox");
    chatbox.scrollTop = chatbox.scrollHeight;
}

export default function MessagesContainer() {
    const client = axios.create({ baseURL: SERVER_API });
    const [loaded, setLoaded] = useState(false);
    const [messages, setMessages] = useState([]);

    const getMessages = () => {
        client.get('/support?id=' + (cookies.get('uid') ?? localStorage.getItem("supportRoom"))).then((rs) => {
            if (rs.data && rs.data.data && rs.data.data.messages) {
                socket.emit('registClient', { client: rs.data.data.client });
                setMessages(s => rs.data.data.messages);
                setLoaded(s => true);
                setTimeout(scrollBottom, 0);
            }
        }).catch(e => {
            if (e.response && e.response.data) {
                toast.error(e.response.data.errors[0]);
            } else {
                toast.error('My ch khng phn hi, vui lng th li');
            }
        });
    }

    useEffect(() => {
        socket.on('sendToClient', (msg) => {
            setMessages(s => {
                return [...s, msg];
            });
            setTimeout(scrollBottom, 0);
        });
        getMessages();
    }, []);

    const sendMessage = (msg) => {

        let sender = {
            client: localStorage.getItem("supportRoom"),
        }
        if (cookies.get('token') && cookies.get('uid')) {
            sender.client = cookies.get('uid');
            sender.token = cookies.get('token');
        }

        socket.emit('clientSend', {
            sender,
            message: msg
        });
        setMessages(s => {
            return [...s, {
                sender: 'request',
                content: msg
            }]
        })
        setTimeout(scrollBottom, 0);
    }


    return (
        <div className="chatbox animated bounceIn">
            <div className="chatbox-container">
                <div className="chatbox-header flex x--between y--center">
                    <MessageHeader />
                </div>
                <div className="chatbox-content scroll flex--y" id="chatbox">
                    <div><div class="message message--in">Cho bn! Bn cn h tr g ?</div></div>
                    <MessageContent messages={messages} />
                </div>
                <div className="chatbox-footer">
                    <SendForm sendMessage={sendMessage} />
                </div>
            </div>
        </div >
    )
}