import Cookies from 'universal-cookie';
import { createSlice } from '@reduxjs/toolkit';

const cookies = new Cookies();
let initial = {
    token: cookies.get('token') ?? null,
    email: cookies.get('email') ?? null,
    fullname: cookies.get('fullname') ?? null,
    uid: cookies.get('uid') ?? null,
};

const { reducer, actions } = createSlice({
    name: 'user',
    initialState: initial,
    reducers: {
        login(state, action) {
            const opt = { path: '/', expires: new Date(Date.now() + 1000 * 60 * 60 * 24 * 30) };
            cookies.set('email', action.payload.data.email, opt);
            cookies.set('fullname', action.payload.data.fullname, opt);
            cookies.set('uid', action.payload.data.id, opt);
            cookies.set('token', action.payload.token, opt);
            cookies.set('level', action.payload.data.level, opt);
            state.token = action.payload.token;
            state.email = action.payload.data.email;
            state.fullname = action.payload.data.fullname;
            state.uid = action.payload.data.id;
            return state;
        },
        logout(state) {
            cookies.remove('uid');
            cookies.remove('email');
            cookies.remove('token');
            cookies.remove('fullname');
            cookies.remove('level');
            localStorage.removeItem("supportRoom");
            window.location.href = './';
        }
    }
});

export const { login, logout } = actions;
export default reducer;